.nav {
  /* background-color: var(--off-white); */
  position: absolute;
  bottom: 6vh;
  width: 100%;
  height: 80px;
  z-index: 1000;
  /* box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15); */
}

.nav-landing {
  bottom: 2em;
}


.nav-logo {
  height: 60px;
  width: 60px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  margin: 0px auto;/* 300px auto; */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.nav-items {
  padding: 0px;
}

.nav-item {
  display: inline;
  margin: 2rem;
  font-size: 30px;
}

.nav-dark {
  color: #fff;
}

.nav-light {
  color: #000;
}

.nav-item:hover {
  color: gray;
  cursor: pointer;
}
#top,
#bottom {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
}

#top {
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 75vh;
}

#bottom {
    height: 25vh;
    bottom: 0;
}

.inner {
    text-align: center;  
}

.inner h1 {
    font-size: 10em;
    margin-top: 5px;
    /* width: 100%; */
}

canvas {
    /* display: none; */
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.nav-bar {
    position: relative;
}

#bottom .nav-item { 
    opacity: 0;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

#bottom:hover .nav-item { 
    opacity: 1;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
#more {
    background-color: gray;
}

