.project {
    height: 50vh;
    width: 100%;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.project:hover .project-colour-dark {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    background-color: rgba(0, 0, 0, 0.75);
}

.project:hover .project-colour-light {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    background-color: rgba(255, 255, 255, 0.75);
}

.project-colour-dark {
    -webkit-transition: 0.1s;
    transition: 0.1s;
    background-color: rgba(0, 0, 0, 0.5);
}

.project-colour-light {
    -webkit-transition: 0.1s;
    transition: 0.1s;
    background-color: rgba(255, 255, 255, 0.5);
}

.project-fade {
    /* position: absolute; */
    width: 100%;
    height: 100%;
}

.project-inner {
    position: absolute;
    text-align: center;
    width: 100%;
    top: 50%; 
    height: 90px;
    margin-top: -54px;
}
 
.project-title {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    margin-top: 0;
    overflow: hidden;
    text-align: center;
    opacity: 1;
}

.project:hover .project-title {
    -webkit-transition: 0.1s;
    transition: 0.1s;
    opacity: 0;
}

#more:hover .project-title {
    -webkit-transition: 0.1s;
    transition: 0.1s;
    opacity: 1;
}

.project-title h1 {
    margin-top: 0;
    text-align: center;
    font-size: 90px;
    font-weight: bolder;
}

.project-info h1 {
    font-size: 90px;
    font-weight: bolder;
}


.project-info {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    display: block;
    margin-top: -160px;
    padding: 0 100px;
    overflow: hidden;
    opacity: 0;
}

.project:hover .project-info {
    -webkit-transition: 0.1s;
    transition: 0.1s;
    opacity: 1;
}


.project-info p {
    margin-top: 0;
    font-size: 28px;
}


.section {
    position: relative;
    color: black;
}

.section-dark {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    background: #282828;
    color: white;
}

.section-container {
    display: flex;
}

.section-half {
    width: 50%;
}

.section-details {
    top: 0;
    position: -webkit-sticky;
    position: sticky;
    /* padding: 40px; */
    height: 100vh;
}

.section-content {
    /* padding: 40px; */
    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.section-info {
    position: absolute;
    margin-top: 34vh;
    margin-left: 10%;
    height: 50%;
    width: 80%;
    text-align: center;
}

.section-info h1 {
    font-size: 130px;
    /* margin-top: 40%; */
    display: none;
}

.section-info p {
    margin-top: 0;
    /* font-weight: bolder; */
    font-size: 48px;
    /* font-family: "Comic Sans MS"; */
    /* display: none; */
}

.section-drop img {
    width: min(80%, 30vh);
}

.section-drop {
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 0;
}

.colour-dark {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    color: white;
}

.colour-light {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    color: black;
}

.section-landing {
    height: 100vh;
    width: 100%;
}

canvas {
    /* display: none; */
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}


